@import '../../styles/colors.scss'; 

.container {
  display: flex;
  position: fixed;
  justify-content: center;
  align-items: center;
  background: white;
  width: 190px;
  height: 268px;
  border-radius: 15px;
}

.img {
  width: calc(100% - 8px);
  height: calc(100% - 8px);
  border: 4px solid $color-third-layot;
  border-radius: inherit;
}

.textImg {
  color: blue;
  font-size: 20px;
}
