%arrow {
  position: relative;
  color: rgb(148, 175, 224);
  text-align: center;
}

.container {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 10px;
  height: 265px;
}

.actionContainer {
  display: flex;
  position: relative;
  background-color: white;
  top: 205px;
  justify-content: space-between;
  width: 180px;
  height: 50px;
  border-radius: 15px;
  padding: 5px;
  z-index: 5;
  transition-property: top, height;
  transition-duration: 0.2s;
  filter: drop-shadow(0px -20px 20px rgba(0, 0, 0, 0.1));
  transition-timing-function: ease-out;
}

.openActionContainer {
  filter: drop-shadow(0px -20px 20px rgba(0, 0, 0, 0.3));
  height: 220px;
  top: 35px;
  transition-property: top, height, filter;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.btnsContainer {
  position: fixed;
  align-self: flex-end;
  margin-left: 140px;
  margin-bottom: 10px;
  z-index: 5;
  border: 0;
  border-bottom: 2px solid rgb(180, 199, 233);
  background-color: white;
  width: 35px;
  height: 35px;
  &:active {
    border: 0;
  }
}

.arrowDown {
  @extend %arrow;
  bottom: 6px;
}

.arrowTop {
  @extend %arrow;
  top: 5px;
}
